import {MouseEventHandler, useEffect, useState} from "react";
import {Box, Grid, IconButton, MenuItem, MenuList, Stack, styled, Typography, useMediaQuery, useTheme} from "@mui/material";
import {ArrowBackIos, ArrowForwardIos} from "@mui/icons-material";
import {useTranslation} from "react-i18next";
import MacosControls from "../common/macosControls";

const HeaderComponent = styled('header')(({theme}) => ({
    display: "flex",
    alignItems: "center",
    justifyContent: "space-between",
    position: "absolute",
    top: 0,
    left: 0,
    right: 0,
    width: "100%",
    height: 75,
    background: theme.palette.primary.light,
    borderTopRightRadius: "inherit",
    borderTopLeftRadius: "inherit",
    padding: 16,
    [theme.breakpoints.up('lg')]: {
        padding: 0,
    },
    "& .MuiTabs-indicator-xs": {
        position: 'absolute',
        bottom: 0,
        left: 0,
        right: 0,
        width: "100%",
        height: '2px',
        background: '#fff',
        "@keyframes myEffect": {
            "0%": {
                width: "100%",
            },
            "100%": {
                width: 0,
            }
        },
        animationName: "myEffect",
        animationDuration: "25s",
        animationTimingFunction: "linear",
        animationIterationCount: "infinite",
    }
}));
const PaginationButton = styled(IconButton)(() => ({
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    color: "inherit",
    transition: "color .25s ease-in-out"
}));
const DesktopNav = styled('nav')(() => ({
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    height: "100%",
    borderRadius: "inherit",
    overflow: "hidden",
    "& .MuiList-root": {
        position: "relative",
        height: "100%",
        display: "flex",
        alignItems: "center",
        borderRadius: "inherit",
    },
    "& .MuiList-root .MuiMenuItem-root": {
        position: "relative",
        height: "100%",
        padding: "8px 32px"
    },
    "& .MuiList-root .MuiMenuItem-root:last-of-type": {borderTopRightRadius: "inherit"},
    "& .MuiTabs-indicator": {
        position: 'absolute',
        bottom: 0,
        display: 'flex',
        height: '2px',
        background: '#fff',
        transition: "all .3s ease-in-out",
    },
}));

interface Props {
    isLoading: boolean,
    tabIndex: number,
    navTabs: any[],
    nextTab: MouseEventHandler,
    previousTab: MouseEventHandler,
    handleChangeTab: Function,
    targetElement?: HTMLElement | null,
}

const Header = ({isLoading, tabIndex, navTabs, previousTab, nextTab, handleChangeTab, targetElement}: Props) => {

    const [indicator, setIndicator] = useState({
        width: 0,
        left: 0,
    });
    const theme = useTheme();
    const isMobile = useMediaQuery(theme.breakpoints.down('lg'));
    const { i18n, t } = useTranslation();
    const isRtl = i18n.language === "fa";

    const handleChangeIndicator = (index: number) => {
        const currentTab = document.querySelector(`.MuiTabSelector-root.MuiTab-${index}`);
        const parentElement = document.querySelector(".MuiTabs-root-lg");
        if (currentTab && parentElement) {
            const eleRect = parentElement.getBoundingClientRect();
            const targetRect = currentTab.getBoundingClientRect();
            const width = currentTab.getBoundingClientRect().width;
            let left;
            if (i18n.language === "fa") {
                left = eleRect.right - targetRect.right;
            } else {
                left = targetRect.left - eleRect.left;
            }
            return setIndicator({width, left});
        }
    }

    useEffect(() => {
        if (isLoading) return;
        const mobileIndicator = document.querySelector(".MuiTabs-indicator-xs");
        const handleTouchstart = () => {
            // @ts-ignore
            mobileIndicator.style.animationPlayState = "paused";
        }
        const handleTouchend = () => {
            // @ts-ignore
            mobileIndicator.style.animationPlayState = "initial";
        }
        if (isMobile) {
            console.log(tabIndex);
            console.log(navTabs.length - 1);
            if (tabIndex >= (navTabs.length - 1)) {
                if (!mobileIndicator) return;
                // @ts-ignore
                mobileIndicator.style.animation = "none";
                // @ts-ignore
                mobileIndicator.style.width = 0;
                // @ts-ignore
                return mobileIndicator.removeEventListener('animationiteration ', nextTab);
            }
            if (targetElement) {
                targetElement.addEventListener('touchstart', handleTouchstart);
                targetElement.addEventListener('touchend', handleTouchend);
            }
            // @ts-ignore
            mobileIndicator?.addEventListener('animationiteration', nextTab);
        }
        function resizeHandler() {
            document.body.style.setProperty('--height', `${window.innerHeight}px`);
            handleChangeIndicator(tabIndex);
        }
        document.fonts.ready.then(resizeHandler);
        window.onresize = () => {
            resizeHandler()
        }
        return () => {
            // @ts-ignore
            mobileIndicator?.removeEventListener('animationiteration', nextTab);
            targetElement?.removeEventListener('touchstart', handleTouchstart);
            targetElement?.removeEventListener('touchend', handleTouchend);
            window.removeEventListener('resize', resizeHandler);
        }
    }, [isLoading, tabIndex]);
    useEffect(() => {
        const mobileIndicator = document.querySelector(".MuiTabs-indicator-xs");
        if (isMobile) {
            if (tabIndex <= (navTabs.length - 1)) {
                if (!mobileIndicator) return;
                // @ts-ignore
                mobileIndicator.style.animationName = "none";
                setTimeout(() => {
                    // @ts-ignore
                    mobileIndicator.style.animationName = "myEffect";
                }, 100);
                // @ts-ignore
                return mobileIndicator.removeEventListener('animationiteration ', nextTab);
            }
        }
        handleChangeIndicator(tabIndex);
    }, [tabIndex]);

    return (
        <HeaderComponent>
            <Box component="nav" display={{ xs: "flex", lg: "none" }} alignItems="center" justifyContent="space-between" width={1}>
                {isRtl
                    ?
                    <>
                        <PaginationButton disabled={isLoading || tabIndex <= 1} onClick={previousTab}><ArrowForwardIos sx={{ fontSize: "1.25rem" }}/></PaginationButton>
                        <Typography variant="h2">{tabIndex > 0 ? t(navTabs[tabIndex].title) : ""}</Typography>
                        <PaginationButton disabled={isLoading || tabIndex === (navTabs.length - 1)} onClick={nextTab}><ArrowBackIos sx={{ fontSize: "1.25rem" }}/></PaginationButton>
                    </>
                    :
                    <>
                        <PaginationButton disabled={isLoading || tabIndex <= 1} onClick={previousTab}><ArrowBackIos sx={{ fontSize: "1.25rem" }}/></PaginationButton>
                        <Typography variant="h2">{tabIndex > 0 ? t(navTabs[tabIndex].title) : ""}</Typography>
                        <PaginationButton disabled={isLoading || tabIndex === (navTabs.length - 1)} onClick={nextTab}><ArrowForwardIos sx={{ fontSize: "1.25rem" }}/></PaginationButton>
                    </>
                }
                <Box className="MuiTabs-indicator-xs" sx={{ width: indicator.width, transform: `translateX(${indicator.left}px)` }}/>
            </Box>
            <Grid container height="100%" display={{ xs: "none", lg: "flex" }} borderRadius="inherit">
                <Grid item lg={3}>
                    <Stack height={1} direction="row" alignItems="center" spacing={2} padding={2}>
                        <MacosControls/>
                        <Stack direction="row" alignItems="center" spacing={.5}>
                            {isRtl
                                ?
                                <>
                                    <PaginationButton disabled={isLoading || tabIndex <= 1} onClick={previousTab}><ArrowForwardIos sx={{ fontSize: "1rem" }}/></PaginationButton>
                                    <PaginationButton disabled={isLoading || tabIndex === (navTabs.length - 1)} onClick={nextTab}><ArrowBackIos sx={{ fontSize: "1rem" }}/></PaginationButton>
                                </>
                                :
                                <>
                                    <PaginationButton disabled={isLoading || tabIndex <= 1} onClick={previousTab}><ArrowBackIos sx={{ fontSize: "1rem" }}/></PaginationButton>
                                    <PaginationButton disabled={isLoading || tabIndex === (navTabs.length - 1)} onClick={nextTab}><ArrowForwardIos sx={{ fontSize: "1rem" }}/></PaginationButton>
                                </>
                            }
                        </Stack>
                    </Stack>
                </Grid>
                <Grid item lg={9} borderRadius="inherit">
                    <DesktopNav>
                        <MenuList disablePadding className="MuiTabs-root-lg">
                            {navTabs.map((item, index) => (
                                item.id && (
                                    <MenuItem key={index} onClick={() => handleChangeTab(index)} className={`MuiTabSelector-root MuiTab-${index}`}>
                                        <Typography variant="h2" fontSize="1rem!important" fontWeight={500}>{t(item.title)}</Typography>
                                    </MenuItem>
                                )
                            ))}
                            <Box className="MuiTabs-indicator" sx={{ width: indicator.width, transform: `translateX(${indicator.left}px)` }}/>
                        </MenuList>
                    </DesktopNav>
                </Grid>
            </Grid>
        </HeaderComponent>
    )
}
export default Header;