import {Stack, styled} from "@mui/material";

const MacosControls = () => {

    const Dot = styled('div')(() => ({
        width: ".8rem",
        height: ".8rem",
        borderRadius: "50%",
    }));

    return (
        <Stack direction="row" alignItems="center" spacing="8.5px">
            <Dot sx={{ background: "#F44336" }}/>
            <Dot sx={{ background: "#FFC107" }}/>
            <Dot sx={{ background: "#4CAF50" }}/>
        </Stack>
    )
}
export default MacosControls;