import {createTheme} from "@mui/material";
import theme from "../theme";

const mainTheme = (isRtl: boolean) => createTheme({
    ...theme,
    typography: {
        fontFamily: [
            isRtl ? "IRANSans" : "Montserrat",
            '-apple-system',
            'BlinkMacSystemFont',
            '"Segoe UI"',
            'Roboto',
            '"Helvetica Neue"',
            'Arial',
            'sans-serif',
            '"Apple Color Emoji"',
            '"Segoe UI Emoji"',
            '"Segoe UI Symbol"',
        ].join(','),
        h1: {
            fontSize: "1.75rem",
            fontWeight: 500,
        },
        h2: {
            fontFamily: isRtl ? "iranyekan!important" : "inherit",
            fontSize: "1.4rem",
            fontWeight: 600,
        },
        h3: {
            fontSize: "1.1rem",
            fontWeight: 600,
            letterSpacing: '0.015em'
        },
        h4: {
            fontSize: "1.75rem",
            fontWeight: 500,
        },
        subtitle1: {
            fontSize: ".95rem",
            fontWeight: 400,
            letterSpacing: "0.0115em",
        },
        body1: {
            fontSize: ".92rem",
            fontWeight: isRtl ? 200 : 300,
        },
        body2: {
            whiteSpace: "break-spaces",
            fontWeight: 300,
        },
        allVariants: {
            color: "#fff"
        }
    },
    direction: isRtl ? "rtl" : "ltr",
});
export default mainTheme;