import {useEffect, useRef, useState} from "react";
import {Box, Container} from "@mui/material";
import Biography from "../../controllers/home/biography";
import Experiences from "../../controllers/home/experiences";
import Skills from "../../controllers/home/skills";
import Projects from "../../controllers/home/projects";
import Contact from "../../controllers/home/contact";
import {Route, Routes, useLocation, useNavigate} from "react-router-dom";
import Wrapper from "./wrapper";
import Footer from "./footer";
import Header from "./header";
import SwipeDetector from "../common/swipeDetector";
import TabPanel from "../common/tabPanel";
import NotFound from "../common/notFound";
import Loading from "../common/loading";
import { withTranslation } from 'react-i18next';

const navTabs = [
    {
        title: "Loading",
    },
    {
        id: 1,
        title: "Biography",
        path: "/",
    },
    {
        id: 2,
        title: "Experiences",
        path: "/experiences",
    },
    {
        id: 3,
        title: "Skills",
        path: "/skills",
    },
    {
        id: 4,
        title: "Projects",
        path: "/projects",
    },
    {
        id: 5,
        title: "Contact",
        path: "/contact",
    },
];

const ContentCard = ({t, i18n}: any) => {

    const [tabIndex, setTabIndex] = useState<number>(0);
    const [isLoading, setIsLoading] = useState<boolean>(true);
    const location = useLocation();
    const navigate = useNavigate();
    const swiperRef = useRef<HTMLDivElement>(null);
    const isRtl = Boolean(i18n.language === "fa");

    const handleChangeTab = async (index: number) => {
        const selectedTab = navTabs[index];
        if (index === tabIndex || !selectedTab.path) return;
        setIsLoading(true);
        await navigate(selectedTab.path);
        setIsLoading(false);
    }
    const nextTab = () => {
        const newTab = tabIndex + 1;
        if (newTab > (navTabs.length - 1)) return;
        handleChangeTab(newTab);
    }
    const previousTab = () => {
        const newTab = tabIndex - 1;
        if (newTab <= 0) return;
        handleChangeTab(newTab);
    }

    useEffect(() => {
        const page = location?.pathname?.split("/")[1];
        if (page) {
            if (typeof page === "string") {
                const lowerCasePage = page.toLowerCase();
                switch (lowerCasePage) {
                    case "experiences": setTabIndex(2); document.title = isRtl ? `   - ${t('Experiences')}` : `Mersad Dolat - ${t('Experiences')}`; break;
                    case "skills": setTabIndex(3); document.title = isRtl ? `   - ${t('Skills')}` : `Mersad Dolat - ${t('Skills')}`; break;
                    case "projects": setTabIndex(4); document.title = isRtl ? `   - ${t('Projects')}` : `Mersad Dolat - ${t('Projects')}`; break;
                    case "contact": setTabIndex(5); document.title = isRtl ? `   - ${t('Contact')}` : `Mersad Dolat - ${t('Contact')}`; break;
                }
            }
        } else {
            document.title = isRtl ? `   - ${t('Biography')}` : `Mersad Dolat - ${t('Biography')}`;
            setTabIndex(1);
        }
        setIsLoading(false);
    }, [location]);

    return (
        <Container maxWidth="xl" sx={{ height: 1, py: { xs: 2.5, lg: 7.5 } }}>
            {swiperRef?.current &&
                <SwipeDetector
                    targetElement={swiperRef.current} onRightSwipe={nextTab} onLeftSwipe={previousTab}
                    updateTrigger={tabIndex} isRtl={isRtl}/>
            }
            <Wrapper ref={swiperRef}>
                <Header
                    isLoading={isLoading} tabIndex={tabIndex}
                    navTabs={navTabs} handleChangeTab={handleChangeTab}
                    nextTab={nextTab} previousTab={previousTab} targetElement={swiperRef?.current}
                />
                <Box height="100%" py="75px" pb={{ lg: 0 }}>
                    <TabPanel index={0} value={tabIndex}>
                        <Loading/>
                    </TabPanel>
                    <Routes>
                        <Route path="/" element={<TabPanel index={1} value={tabIndex}><Biography/></TabPanel>}/>
                        <Route path="experiences" element={<TabPanel index={2} value={tabIndex}><Experiences/></TabPanel>}/>
                        <Route path="skills" element={<TabPanel index={3} value={tabIndex}><Skills/></TabPanel>}/>
                        <Route path="projects" element={<TabPanel index={4} value={tabIndex}><Projects/></TabPanel>}/>
                        <Route path="contact" element={<TabPanel index={5} value={tabIndex}><Contact/></TabPanel>}/>
                        <Route path="*" element={<NotFound/>}/>
                    </Routes>
                </Box>
                <Footer/>
            </Wrapper>
        </Container>
    )
}
export default withTranslation()(ContentCard);