import { useEffect } from "react";

interface Props {
    targetElement: Element;
    onRightSwipe?: Function;
    onLeftSwipe?: Function;
    onUpSwipe?: Function;
    onDownSwipe?: Function;
    updateTrigger?: any;
    isRtl?: boolean;
}

const options = {
    threshold: 70, //required min distance traveled to be considered swipe
    restraint: 100, // maximum distance allowed at the same time in perpendicular direction
    allowedTime: 300, // maximum time allowed to travel that distance
}
const states = {
    swipeDir: 'none',
    startX: 0,
    startY: 0,
    distX: 0,
    distY: 0,
    elapsedTime: 0,
    startTime: 0,
};

const SwipeDetector = ({targetElement, onRightSwipe, onLeftSwipe, onUpSwipe, onDownSwipe, updateTrigger, isRtl}: Props) => {

    let {swipeDir, startX, startY, distY, distX, startTime, elapsedTime } = states;
    const {threshold, restraint, allowedTime} = options;


    useEffect(() => {
        // @ts-ignore
        const touchstartHandler = (e) => {
            const touchObj = e.changedTouches[0]
            swipeDir = 'none'
            startX = touchObj.pageX
            startY = touchObj.pageY
            startTime = new Date().getTime() // record time when finger first makes contact with surface
        }
        // @ts-ignore
        const touchendHandler = (e) => {
            const touchObj = e.changedTouches[0]
            distX = touchObj.pageX - startX // get horizontal dist traveled by finger while in contact with surface
            distY = touchObj.pageY - startY // get vertical dist traveled by finger while in contact with surface
            elapsedTime = new Date().getTime() - startTime // get time elapsed
            if (elapsedTime <= allowedTime){ // first condition for swipe met
                if (Math.abs(distX) >= threshold && Math.abs(distY) <= restraint){ // 2nd condition for horizontal swipe met
                    if (isRtl) {
                        swipeDir = (distX < 0) && onLeftSwipe ? onLeftSwipe() : onRightSwipe && onRightSwipe() // if dist traveled is negative, it indicates left swipe
                    } else {
                        swipeDir = (distX < 0) && onRightSwipe ? onRightSwipe() : onLeftSwipe && onLeftSwipe() // if dist traveled is negative, it indicates left swipe
                    }
                }
                else if (Math.abs(distY) >= threshold && Math.abs(distX) <= restraint){ // 2nd condition for vertical swipe met
                    swipeDir = (distY < 0) && onUpSwipe ? onUpSwipe() : onDownSwipe && onDownSwipe() // if dist traveled is negative, it indicates up swipe
                }
            }
        }
        if (targetElement) {
            targetElement.addEventListener("touchstart", touchstartHandler);
            targetElement.addEventListener('touchend', touchendHandler);
        }
        return () => {
            targetElement?.removeEventListener('touchstart', touchstartHandler);
            targetElement?.removeEventListener('touchend', touchendHandler);
        }
    }, [updateTrigger]);

    return null;
}
export default SwipeDetector;