import {Box, Stack, Typography} from "@mui/material";
import {OpenInNewRounded} from "@mui/icons-material";
import {useTranslation, withTranslation} from "react-i18next";
import {motion} from "framer-motion";

const wrapperStyles = {
    width: { xs: 1, lg: "25vw" },
    height: { xs: "50vw", lg: "15vw" },
    borderRadius: 1,
    borderWidth: 'thin',
    borderStyle: 'solid',
    borderColor: '#FFD600',
    boxShadow: '0px 5px 15px 3px rgb(255 214 0 / 20%)',
};

interface ProjectProps {
    name: string;
    imgSrc: string;
    href?: string;
}
const Project = ({name, imgSrc, href} : ProjectProps) => {

    const {t} = useTranslation();

    return (
        <Box component={motion.div} variants={item} initial="hidden" animate='none' whileInView="visible" viewport={{ once: true }} display="flex" alignItems="center" flexDirection={{ xs: "column", lg: "row" }}>
            <Box sx={wrapperStyles}>
                <Box component="img" src={imgSrc} alt="Project" width="100%" height="100%" sx={{ borderRadius: "inherit", objectFit: "cover" }}/>
            </Box>
            <Box mt={{ xs: 2, lg: 0 }} ml={{ lg: 4 }} flex={1}>
                <Typography variant="h5" component="p" textAlign={{ xs: "center", lg: "initial" }}>{t(name)}</Typography>
                {href
                    ?
                    <Stack component="a" href={href} target="_blank"
                           direction="row" alignItems="center" justifyContent={{ xs: "center", lg: "initial" }} spacing={.5} sx={{ color: theme => theme.palette.primary.main }}>
                        <Typography color="inherit" variant="button">{t('visit')}</Typography>
                        <OpenInNewRounded sx={{fontSize: { xs: "1.1rem" }}}/>
                    </Stack>
                    :
                    <Typography textAlign={{ xs: "center", lg: "initial" }}>{t('linkUnavailable')}</Typography>
                }
            </Box>
        </Box>
    )
}

const Projects = ({t}: any) => {
    return (
        <Box pb={{ xs: 2, lg: 12 }} component={motion.section} initial="hidden" animate="visible" variants={container}>
            <Typography variant="h3" fontSize={{ lg: "1.2rem" }}>{t('someOfProjects')}</Typography>
            <Stack mt={5} spacing={5}>
                <Project name="golgoharProject" imgSrc="/assets/image/projects/golgoharsport.jpg" href="https://golgoharsport.ir"/>
                <Project name="saeidSadeghiProject" imgSrc="/assets/image/projects/saeid-sadeghi.jpg" href="https://saeidsadeghi.com"/>
                <Project name="hikaCmsProject" imgSrc="/assets/image/projects/hikacms.jpg"/>
            </Stack>
        </Box>
    )
}
export default withTranslation()(Projects);

// Motions
const container = {
    hidden: { opacity: 0 },
    visible: {
        opacity: 1,
        transition: {
            delay: .1,
            duration: .75,
            delayChildren: .75,
            staggerChildren: .3
        }
    }
}
const item = {
    hidden: { y: 200, opacity: 0 },
    visible: {
        y: 0,
        opacity: 1,
        transition: {
            duration: 1,
        }
    }
}