import {CssBaseline, GlobalStyles, responsiveFontSizes, ThemeProvider} from "@mui/material";
import {PropsWithChildren, useEffect, useState} from "react";
import mainTheme from "../themes/home";
import {useTranslation} from "react-i18next";
import rtlPlugin from 'stylis-plugin-rtl';
import {CacheProvider} from '@emotion/react';
import createCache from '@emotion/cache';
// @ts-ignore
import {prefixer} from 'stylis';


const MainLayout = ({children}: PropsWithChildren) => {

    const [isRtl, setIsRtl] = useState<boolean>(false);
    const {i18n} = useTranslation();

    useEffect(() => {
        if (i18n?.language === "fa") {
            document.dir = "rtl";
            document.documentElement.lang = "fa-IR";
            setIsRtl(true);
        }
    }, []);

    // Create rtl cache
    const cache = createCache({
        key: "css"
    });
    const cacheRtl = createCache({
        key: 'muirtl',
        stylisPlugins: [prefixer, rtlPlugin],
    });

    return (
        <CacheProvider value={isRtl ? cacheRtl : cache }>
            <ThemeProvider theme={responsiveFontSizes(mainTheme(isRtl))}>
                <CssBaseline/>
                <GlobalStyles styles={`@import url("/assets/css/layouts/main.css");`}/>
                {isRtl && <GlobalStyles styles={`@import url("/assets/css/layouts/main-rtl.css");`}/>}
                <main>
                    {children}
                </main>
            </ThemeProvider>
        </CacheProvider>
    )
}
export default MainLayout;