import {Box, Grid, List, Stack, Typography} from "@mui/material";
import { withTranslation } from "react-i18next";
import {motion} from "framer-motion";

const Biography = ({i18n, t}: any) => {
    return (
        <Box pb={{ xs: 2, lg: 12 }} component={motion.section} variants={container} initial="hidden" animate="visible">
            <Box display="flex" alignItems={{ xs: "center" }} flexDirection={{ xs: "column", lg: "row" }}>
                <Box component={motion.div} variants={container} sx={{ width: 128, height: 128, borderRadius: "50%", border: "3px solid #fff", boxShadow: "0 1px 12px 1px rgb(208 198 198 / 15%)" }}>
                    <Box component='img' src="/assets/image/IMG2.jpg" width="100%" height="100%" alt="Person" sx={{ borderRadius: "inherit", objectFit: "cover", transition: "transform .35s ease-in-out", transform: i18n.language === "fa" ? 'rotateY(180deg)' : "" }}/>
                </Box>
                <Box mt={{ xs: 2, lg: 0 }} ml={{ lg: 3 }}>
                    <Typography component={motion.h1} variants={item} variant="h1" textAlign={{ xs: "center", lg: "initial" }}>{t('name')}</Typography>
                    <Typography component={motion.h4} variants={item} mt={{ xs: 1, lg: 1.5 }} variant="h4" textAlign={{ xs: "center", lg: "initial" }}>{t('title')}</Typography>
                </Box>
            </Box>
            <Grid mt={0} pb={{ xs: 2, lg: 0 }} container spacing={3}>
                <Grid component={motion.div} variants={container} item xs={12} lg={6}>
                    <Typography component={motion.h3} variants={item} variant="h3" fontSize={{ lg: "1.2rem" }}>{t('personalInfo')}</Typography>
                    <List component={motion.ul} variants={container}>
                        <Stack component={motion.li} variants={item} direction="row" alignItems="center" spacing={1}>
                            <Typography variant="subtitle1" component="p" fontSize={{ lg: "1rem" }}>{t('age')}</Typography>
                            <Typography variant="body1" fontSize={{ lg: ".98rem" }}>{t('ageValue')}</Typography>
                        </Stack>
                        <Stack component={motion.li} variants={item} direction="row" alignItems="center" spacing={1}>
                            <Typography variant="subtitle1" component="p" fontSize={{ lg: "1rem" }}>{t('location')}</Typography>
                            <Typography variant="body1" fontSize={{ lg: ".98rem" }}>{t('locationValue')}</Typography>
                        </Stack>
                        <Stack component={motion.li} variants={item} direction="row" alignItems="center" spacing={1}>
                            <Typography variant="subtitle1" component="p" fontSize={{ lg: "1rem" }}>{t('education')}</Typography>
                            <Typography variant="body1" fontSize={{ lg: ".98rem" }}>{t('educationValue')}</Typography>
                        </Stack>
                        <Stack component={motion.li} variants={item} direction="row" alignItems="center" spacing={1}>
                            <Typography variant="subtitle1" component="p" fontSize={{ lg: "1rem" }}>{t('yearsOfExperience')}</Typography>
                            <Typography variant="body1" fontSize={{ lg: ".98rem" }}>{t('yearsOfExperienceValue')}</Typography>
                        </Stack>
                    </List>
                </Grid>
                <Grid component={motion.div} variants={container} item xs={12} lg={6}>
                    <Typography component={motion.h3} variants={item} variant="h3" fontSize={{ lg: "1.2rem" }}>{t('aboutMe')}</Typography>
                    <Typography component={motion.p} variants={item} mt={1} variant="body2" fontSize={{ lg: "1rem" }}>{t('aboutMeValue')}</Typography>
                </Grid>
            </Grid>
        </Box>
    )
}
export default withTranslation()(Biography);

// Motions
const container = {
    hidden: { opacity: 0, y: -100 },
    visible: {
        opacity: 1,
        y: 0,
        transition: {
            delay: .25,
            duration: .85,
            delayChildren: .5,
            staggerChildren: .3
        }
    }
}
const item = {
    hidden: { opacity: 0 },
    visible: {
        opacity: 1
    }
}