import {Box, Stack, Typography} from "@mui/material";
import {withTranslation} from "react-i18next";
import {motion} from "framer-motion";

interface SkillProps {
    logo: string;
    title: string;
    proficiency: string;
}
const Skill = ({logo, title, proficiency}: SkillProps) => {
    return (
        <Stack component="li" direction="row" alignItems="center" spacing={1}>
            <Box display="flex" alignItems="center">
                <Box component="img" src={logo} width={32} height={32} alt="Logo"/>
                <Typography ml={.5} variant="subtitle1" fontSize={{lg: "1rem"}}>{title}</Typography>
            </Box>
            <Typography variant="body1" fontSize={{lg: ".98rem"}}>{proficiency}</Typography>
        </Stack>
    )
};
const skills = [
    {
        title: "HTML:",
        proficiency: "Advanced",
        logo: "/assets/image/logos/html-logo.svg"
    },
    {
        title: "CSS:",
        proficiency: "Advanced",
        logo: "/assets/image/logos/css-logo.svg"
    },
    {
        title: "Javascript:",
        proficiency: "Advanced",
        logo: "/assets/image/logos/js-logo.svg"
    },
    {
        title: "React.js:",
        proficiency: "Advanced",
        logo: "/assets/image/logos/reactjs-logo.svg"
    },
    {
        title: "Next.js:",
        proficiency: "Advanced",
        logo: "/assets/image/logos/nextjs-logo.svg"
    },
    {
        title: "Typescript:",
        proficiency: "Intermediate",
        logo: "/assets/image/logos/ts-logo.svg"
    },
    {
        title: "Material UI:",
        proficiency: "Advanced",
        logo: "/assets/image/logos/mui-logo.svg"
    },
];
const otherSkills = [
    {
        title: "Framer:",
        proficiency: "Intermediate",
        logo: "/assets/image/logos/framer-logo.webp"
    },
    {
        title: "Figma:",
        proficiency: "Intermediate",
        logo: "/assets/image/logos/figma.svg"
    },
    {
        title: "NodeJS:",
        proficiency: "Basic",
        logo: "/assets/image/logos/nodejs-logo.svg"
    },
    {
        title: "Bootstrap:",
        proficiency: "Advanced",
        logo: "/assets/image/logos/bootstrap-logo.svg"
    },
    {
        title: "jQuery:",
        proficiency: "Intermediate",
        logo: "/assets/image/logos/jquery-logo.svg"
    },
];

const Skills = ({t}: any) => {
    return (
        <Box component={motion.div} initial="hidden" animate="visible" variants={container} pb={{ xs: 2, lg: 0 }}>
            <Typography variant="h3" fontSize={{ lg: "1.2rem" }} textAlign="center">{t('currentSkills')}</Typography>
            <Stack component="ul" m={0} mt={2} p={0} direction={{ lg: "row" }}
                   alignItems={{ lg: "center" }} justifyContent="initial" flexWrap={{ lg: "wrap" }} rowGap={{ xs: 2, lg: 4 }} columnGap={{ xs: 2, lg: 6 }}>
                {skills.map((s, index) => (
                    <Skill key={index} logo={s.logo} title={s.title} proficiency={t(s.proficiency)}/>
                ))}
            </Stack>
            <Typography mt={6} variant="h3" fontSize={{ lg: "1.2rem" }} textAlign="center">{t('otherSkills')}</Typography>
            <Stack component="ul" m={0} mt={2} p={0} direction={{ lg: "row" }}
                   alignItems={{ lg: "center" }} justifyContent="initial" flexWrap={{ lg: "wrap" }} rowGap={{ xs: 2, lg: 4 }} columnGap={{ xs: 2, lg: 6 }}>
                {otherSkills.map((s, index) => (
                    <Skill key={index} logo={s.logo} title={s.title} proficiency={t(s.proficiency)}/>
                ))}
            </Stack>
        </Box>
    )
}
export default withTranslation()(Skills);

// Motions
const container = {
    hidden: { opacity: 0, x: -200 },
    visible: {
        opacity: 1,
        x: 0,
        transition: {
            delay: .1,
            duration: .75,
            delayChildren: .75,
            staggerChildren: .3
        }
    }
}