import {createTheme} from "@mui/material";


const theme = createTheme({
    palette: {
        primary: {
            main: "#FFD600",
            light: 'rgba(255, 214, 0, 0.35)',
            100: "#ead880",
        }
    },
    breakpoints: {
        values: {
            xs: 0,
            sm: 360,
            md: 750,
            lg: 990,
            xl: 1200,
            xxl: 1400,
        },
    },
    components: {
        MuiContainer: {
            defaultProps: {
                maxWidth: "xxl"
            }
        },
        MuiIconButton: {
            styleOverrides: {
                root: {
                    "&.Mui-disabled": {
                        color: "#8D8D89",
                    }
                }
            }
        },
    }
});
export default theme;

declare module '@mui/material/styles' {
    interface BreakpointOverrides {
        xs: true;
        sm: true;
        md: true;
        lg: true;
        xl: true;
        xxl: true;
    }

    interface CustomTheme {
    }

    interface Theme extends CustomTheme {
    }

    interface ThemeOptions extends CustomTheme {
    }
}
