import {
    Box,
    Button,
    ClickAwayListener,
    FormControl,
    FormControlLabel,
    Modal,
    Radio,
    RadioGroup,
    SelectChangeEvent,
    Stack,
    styled,
    Typography
} from "@mui/material";
import {useTranslation} from "react-i18next";
import {useNavigate} from "react-router-dom";

const Wrapper = styled(Box)(({theme}) => ({
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-between",
    background: 'rgba(51, 51, 51, 0.8)',
    boxShadow: '0 2.5px 16px 2px rgb(234 216 128 / 8%)',
    padding: 26,
    width: '70%',
    height: '50%',
    borderRadius: 10,
    border: '.75px solid rgb(209 209 209 / 15%)',
    [theme.breakpoints.up('lg')]: {
        width: "25%"
    }
}));

interface LangProps {
    lang: string;
}
const LangItem = ({lang}: LangProps) => {

    switch (lang) {
        case "fa": return (
            <Stack direction="row" alignItems="center" spacing={1}>
                <Box component="img" src="/assets/image/logos/iran.png" width={26} height={26} sx={{ objectFit: "cover", borderRadius: 1 }}/>
                <Typography></Typography>
            </Stack>
        )
        default: return (
            <Stack direction="row" alignItems="center" spacing={1}>
                <Box component="img" src="/assets/image/logos/us.svg" width={26} height={26} sx={{ objectFit: "cover", borderRadius: 1 }}/>
                <Typography>English</Typography>
            </Stack>
        )
    }
}

interface SelectLanguageProps {
    open: boolean,
    onClose: any;
}
const SelectLanguageModal = ({open, onClose}: SelectLanguageProps) => {

    const {i18n, t} = useTranslation();
    const navigate = useNavigate();

    const handleChangeLang = (e: SelectChangeEvent) => {
        i18n.changeLanguage(e?.target?.value)
            .then(() => navigate(0));
    }

    return (
        <Modal open={open} onClose={onClose}>
            <Box width={1} height={1} display="flex" alignItems="center" justifyContent="center">
                <ClickAwayListener onClickAway={onClose}>
                    <Wrapper>
                        <Box>
                            <Typography fontSize={{ xs: "1rem" }} fontWeight={500} textAlign="center">{t('selectLang')}</Typography>
                            <FormControl sx={{ mt: 3 }}>
                                <RadioGroup
                                    value={i18n?.resolvedLanguage}
                                    onChange={handleChangeLang}>
                                    <FormControlLabel value="en" control={<Radio sx={{ color: "#ead880" }}/>} label={<LangItem lang="en"/>}/>
                                    <FormControlLabel value="fa" control={<Radio sx={{ color: "#ead880" }}/>} label={<LangItem lang="fa"/>}/>
                                </RadioGroup>
                            </FormControl>
                        </Box>
                        <Button onClick={onClose} fullWidth sx={{ background: theme => theme.palette.primary.light + "!important", color: "#fff" }}>{t("close")}</Button>
                    </Wrapper>
                </ClickAwayListener>
            </Box>
        </Modal>
    )
}
export default SelectLanguageModal;