import {Box, Typography} from "@mui/material";
import Timeline from '@mui/lab/Timeline';
import TimelineItem from '@mui/lab/TimelineItem';
import TimelineSeparator from '@mui/lab/TimelineSeparator';
import TimelineConnector from '@mui/lab/TimelineConnector';
import TimelineContent from '@mui/lab/TimelineContent';
import {withTranslation} from "react-i18next";
import {motion} from "framer-motion";

const timelineStyles = {
    p: 0,
    m: 0,
    mt: 2,
}

const Experiences = ({t}: any) => {
    return (
        <Box pb={{ xs: 2, lg: 12 }} component={motion.div} initial="hidden" animate="visible" variants={container} display="flex" alignItems="center" justifyContent="center" width={1}>
            <Timeline position="alternate" sx={timelineStyles}>
                <TimelineItem>
                    <TimelineSeparator>
                        <TimelineConnector />
                    </TimelineSeparator>
                    <TimelineContent>
                        <Box component={motion.div} variants={item} width={1} display="flex" alignItems="center" justifyContent={{ xs: "center", lg: "initial" }} flexDirection={{ xs: "column", lg: "row" }}>
                            <Box sx={{ width: {xs: 45 , lg: 50}, height: {xs: 45, lg: 50}, borderRadius: "50%" }}>
                                <Box component="img" src="/assets/image/novinverse-logo.jpg" alt="Logo" width="100%" height="100%" sx={{borderRadius: "inherit", objectFit: "cover"}}/>
                            </Box>
                            <Box mt={{ xs: .5, lg: 0 }} ml={{ lg: 1 }}>
                                <Typography variant="subtitle1" textAlign={{ xs: 'center', lg: "initial" }}>Novinverse</Typography>
                                <Typography variant="body1" fontSize={{ xs: ".82rem" }} textAlign={{ xs: 'center', lg: "initial" }}>{t('totalTime')}</Typography>
                            </Box>
                        </Box>
                    </TimelineContent>
                </TimelineItem>
                <TimelineItem>
                    <TimelineSeparator>
                        <TimelineConnector />
                    </TimelineSeparator>
                    <TimelineContent></TimelineContent>
                </TimelineItem>
                <TimelineItem>
                    <TimelineSeparator>
                        <TimelineConnector />
                    </TimelineSeparator>
                    <TimelineContent></TimelineContent>
                </TimelineItem>
                <TimelineItem>
                    <TimelineSeparator>
                        <TimelineConnector />
                    </TimelineSeparator>
                    <TimelineContent></TimelineContent>
                </TimelineItem>
            </Timeline>
        </Box>
    )
}
export default withTranslation()(Experiences);

// Motions
const container = {
    hidden: { opacity: 0, x: -200 },
    visible: {
        opacity: 1,
        x: 0,
        transition: {
            delay: .1,
            duration: .75,
            delayChildren: .75,
            staggerChildren: .3
        }
    }
}
const item = {
    hidden: { opacity: 0 },
    visible: {
        opacity: 1
    }
}