import {Box, Stack, Typography} from "@mui/material";
import {EmailRounded, GitHub, LinkedIn, PhoneRounded, Telegram} from "@mui/icons-material";
import {withTranslation} from "react-i18next";
import {motion} from "framer-motion";

const Contact = ({t, i18n}: any) => {
    return (
        <Box pb={{ xs: 2, lg: 12 }} component={motion.section} initial="hidden" animate="visible" variants={container} display="flex" alignItems="center" flexDirection="column" width={1} height={1}>
            <Stack component={motion.div} variants={item} direction={{ lg: "row" }} alignItems="center" spacing={3} rowGap={2}>
                <Stack component="a" href="mailto:mersaddolatbadi@gmail.com" direction="row" alignItems="center" spacing={1} sx={{ color: "#fff", textDecoration: "none" }}>
                    <EmailRounded/>
                    <Typography variant="subtitle1" letterSpacing={1}>MersadDolatAbadi@gmail.com</Typography>
                </Stack>
                <Stack component="a" href="tel:+989909953698" direction="row" alignItems="center" spacing={1} sx={{ color: "#fff", textDecoration: "none" }}>
                    <PhoneRounded/>
                    <Typography variant="subtitle1" letterSpacing={1.5}>{t('phoneNumber')}</Typography>
                </Stack>
            </Stack>
            <Stack component={motion.div} variants={item} mt={3} direction="row" alignItems="center" justifyContent="center" spacing={5}>
                <a href="https://www.linkedin.com/in/mersaddolat/" target="_blank" rel="noreferrer">
                    <LinkedIn sx={{ fontSize: {xs: "1.7rem"}, color: "#fff" }}/>
                </a>
                <a href="https://github.com/mersaddolat" target="_blank" rel="noreferrer">
                    <GitHub sx={{ fontSize: {xs: "1.7rem"}, color: "#fff" }}/>
                </a >
                <a href="https://t.me/MerSaD5547" target="_blank" rel="noreferrer" >
                    <Telegram sx={{ fontSize: {xs: "1.7rem"}, color: "#fff" }}/>
                </a>
            </Stack>
        </Box>
    );
}
export default withTranslation()(Contact);

// Motions
const container = {
    hidden: { opacity: 0, x: -200 },
    visible: {
        opacity: 1,
        x: 0,
        transition: {
            delay: .1,
            duration: .75,
            delayChildren: .25,
            staggerChildren: .3
        }
    }
}
const item = {
    hidden: { opacity: 0 },
    visible: {
        opacity: 1
    }
}