import {ButtonBase, Divider, Stack, styled, Typography} from "@mui/material";
import {Download, OpenInNewRounded, PublicRounded} from "@mui/icons-material";
import SelectLanguageModal from "../common/selectLanguageModal";
import {useRef, useState} from "react";
import {useTranslation} from "react-i18next";

const FooterComponent = styled('footer')(({theme}) => ({
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    width: "100%",
    height: 75,
    // padding: 16,
    position: "absolute",
    bottom: 0,
    left: 0,
    right: 0,
    background: theme.palette.primary.light,
    borderBottomRightRadius: "inherit",
    borderBottomLeftRadius: "inherit",
    [theme.breakpoints.up('lg')]: {
        background: "transparent",
        height: "auto",
    }
}));
const FooterButton = styled(ButtonBase)(({theme}) => ({
    width: "100%",
    height: "100%",
    color: "#fff",
    borderRadius: "inherit",
    "&:first-of-type": {
        borderTopRightRadius: 0,
        borderBottomRightRadius: 0
    },
    "&:last-of-type": {
        borderTopLeftRadius: 0,
        borderBottomLeftRadius: 0
    },
    "& .MuiSvgIcon-root": {
        fontSize: "1.2rem",
    },
    "& .MuiTypography-root": {
        fontSize: ".8rem",
        fontWeight: 500,
    },
    [theme.breakpoints.up("lg")]: {
        width: "auto",
        height: "auto",
        padding: "20px 32px",
        background: theme.palette.primary.light,
        borderRadius: 25,
        "&:first-of-type": {
            borderTopLeftRadius: 0,
            borderTopRightRadius: 25,
            borderBottomRightRadius: 0
        },
        "&:last-of-type": {
            borderTopRightRadius: 0,
            borderTopLeftRadius: 25,
            borderBottomLeftRadius: 0
        },
    }
}));


const Footer = () => {

    const [isSelectModalOpen, setSelectModalOpen] = useState<boolean>(false);
    const {i18n, t} = useTranslation();
    const aRef = useRef<HTMLAnchorElement>(null);

    const FindLanguage = () => {
        switch (i18n.language) {
            case "fa": return <Typography></Typography>;
            default: return <Typography>English</Typography>;
        }
    }
    const handleDownloadCV = () => {
        if (aRef?.current) {
            aRef.current.click();
        }
    }

    return (
        <>
            <FooterComponent>
                <Stack width="100%" height="100%" direction="row" alignItems={{ xs: "center", lg: 'flex-end' }} justifyContent="space-between"
                       divider={<Divider orientation="vertical" color="white" sx={{display: {lg: "none"}, height: "70%" }}/>}
                       borderRadius="inherit">
                    <FooterButton onClick={handleDownloadCV}>
                        <a href={i18n.language === "fa" ? "/assets/documents/cv-fa.pdf" : "/assets/documents/cv.pdf"} ref={aRef} style={{ display: "none" }}></a>
                        <Download/>
                        <Typography ml={.5}>{t('cv')}</Typography>
                    </FooterButton>
                    <FooterButton onClick={() => setSelectModalOpen(true)}>
                        <PublicRounded sx={{ mr: .5 }}/>
                        <FindLanguage/>
                    </FooterButton>
                </Stack>
            </FooterComponent>
            <SelectLanguageModal open={isSelectModalOpen} onClose={() => setSelectModalOpen(false)}/>
        </>
    )
}
export default Footer